import React, { useEffect, useState } from 'react';
import axios from './axios';
import "./Row.css";
import Youtube from 'react-youtube';
import movieTrailer from 'movie-trailer';

function Row({ title, fetchURL, isLarge }) {
    const [movies, setMovies] = useState([]);
    const [trailerURL, setTrailerURL] = useState("");

    const base_url = "https://image.tmdb.org/t/p/original/"

    useEffect(() => {
        //[] means run once when the row loads and never again
        // if [movies] means run every time "movies" changes

        async function fetchData() {
            const request = await axios.get(fetchURL);
            setMovies(request.data.results);
            return request;
        }
        fetchData();

    }, [fetchURL]);

    const opts = {
        height: '390',
        width: '640',
        playerVars: {
            // https://developers.google.com/youtube/player_parameters
            autoplay: 1,
        },
    };

    const handleClick = (movieItem) => {
        if (trailerURL) {
            setTrailerURL("");
        } else {
            //     movieTrailer(movieItem?.name || "")
            //         .then((url) => {
            //             const urlParams = new URLSearchParams(new URL(url).search);
            //             setTrailerURL(urlParams.get('v'));
            //         }).catch((error) => console.log(error));
        }
    }

    return (
        <div className="row">
            <h2>{title}</h2>
            <div className="row__posters">
                {movies.map(movieItem => (
                    <div className="row__postersContainer">
                        <img key={movieItem.id}
                            onClick={handleClick(movieItem)}
                            className={`row__poster ${isLarge && "row__posterLarge"}`}
                            src={`${base_url}${isLarge ? movieItem.poster_path : movieItem.backdrop_path}`} alt={movieItem.name} />
                        <div className="row__posterText">{movieItem?.title || movieItem?.name || movieItem?.original_name || ""}</div>
                    </div>
                ))}
            </div>
            {trailerURL && <Youtube videoId={trailerURL} opts={opts} />}
        </div>
    );
}

export default Row
